import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

// Функция для проверки состояния туннеля
const checkTunnelStatus = async () => {
  try {
    const response = await fetch('/health');
    if (!response.ok) {
      throw new Error('Tunnel health check failed');
    }
    return await response.json();
  } catch (error) {
    console.error('Tunnel status check failed:', error);
    return { status: 'error', error: error.message };
  }
};

export const api = createApi({
  baseQuery: fetchBaseQuery({ 
    baseUrl: '/api',
    credentials: 'include',
    prepareHeaders: async (headers, { getState }) => {
      // Проверяем состояние туннеля перед каждым запросом
      const tunnelStatus = await checkTunnelStatus();
      if (tunnelStatus.status !== 'OK') {
        throw new Error('Tunnel connection is not available');
      }
      return headers;
    }
  }),
  endpoints: (builder) => ({
    getProducts: builder.query({
      query: () => 'products'
    }),
    getProduct: builder.query({
      query: (id) => `products/${id}`
    }),
    addProduct: builder.mutation({
      query: (product) => ({
        url: 'products',
        method: 'POST',
        body: product
      })
    }),
    // Добавляем эндпоинт для проверки состояния туннеля
    checkTunnelHealth: builder.query({
      query: () => 'health',
      transformResponse: (response) => ({
        ...response,
        checkedAt: new Date().toISOString()
      })
    })
  })
});

export const {
  useGetProductsQuery,
  useGetProductQuery,
  useAddProductMutation,
  useCheckTunnelHealthQuery
} = api; 